<template>
  <div class="Top">
    <a v-for="item in items" :key="item.id" :href="item.title">
      <div>
        <div>{{ item.title }}</div>
        <div>{{ item.description }}</div>
      </div>
    </a>
  </div>
</template>

<script>
// const json = () =>
//   import(/* webpackPrefetch: true */ '../../content/summary.json')
import json from '../../content/top.json'

export default {
  name: 'Top',
  data() {
    return { items: [] }
  },
  mounted() {
    for (var i = 0; i < json.length; i++) {
      console.log(json[i])

      const readData = json[i]
      this.items.push({
        id: i,
        link: readData.linkPath,
        title: readData.title,
        description: readData.description
      })
    }
  }
}
</script>

<style scoped></style>



